* {
  box-sizing: border-box; }

body {
  background-color: #1e1e1e;
  color: #fff;
  font-family: "Circular", Proxima Nova, Open Sans, Source Sans Pro, sans-serif;
  font-size: 16px; }

button {
  padding: 0;
  border: 0;
  background: none;
  color: #fff; }

a {
  color: inherit;
  text-decoration: none; }

@keyframes logo--smaller {
  0% {
    opacity: 1; }
  50% {
    opacity: 0;
    top: 24px;
    font-size: 22px; }
  51% {
    opacity: 0;
    width: 46px;
    top: 16px;
    left: 30px;
    font-size: 40px; }
  100% {
    opacity: 1;
    width: 46px;
    top: 16px;
    left: 30px;
    font-size: 40px; } }

@keyframes logo--default {
  0% {
    opacity: 1;
    width: 46px;
    top: 16px;
    left: 30px;
    font-size: 40px; }
  50% {
    opacity: 0;
    width: 46px;
    top: 16px;
    left: 30px;
    font-size: 40px; }
  51% {
    opacity: 0;
    width: auto;
    top: 24px;
    font-size: 22px; }
  100% {
    opacity: 1;
    width: auto;
    top: 24px;
    font-size: 22px; } }

.logo {
  font-size: 0; }
  .logo:before {
    font-size: 20px; }
  .logo--sidebar {
    width: 100%;
    height: 70px;
    margin-bottom: 40px;
    background-color: #6863ce;
    transition: 0.1s background ease-in-out; }
    .logo--sidebar:hover {
      background-color: #706bd0; }
    .logo--sidebar:before {
      position: absolute;
      top: 24px;
      left: 30px;
      font-size: 22px;
      overflow: hidden;
      pointer-events: none; }
      .sidebar--loaded:not(.sidebar--collapsed) .logo--sidebar:before {
        animation: logo--default 0.6s forwards alternate ease-in-out; }
      .sidebar--collapsed .logo--sidebar:before {
        animation: logo--smaller 0.6s forwards alternate ease-in-out; }

.logo__link {
  display: block;
  width: 100%;
  height: 100%; }

@keyframes usage--sidebar-hide {
  0% {
    opacity: 1; }
  50% {
    opacity: 0;
    min-width: 175px; }
  51% {
    min-width: 0; }
  100% {
    opacity: 1;
    min-width: 0; } }

@keyframes usage--sidebar-show {
  0% {
    opacity: 1;
    min-width: 0;
    max-width: 96px; }
  50% {
    opacity: 0;
    min-width: 0;
    max-width: 96px; }
  51% {
    min-width: 175px;
    max-width: 100%; }
  100% {
    opacity: 1;
    min-width: 175px;
    max-width: 100%; } }

@keyframes usage--flex-hide {
  0% {
    flex-direction: row;
    text-align: left; }
  99% {
    flex-direction: row;
    text-align: left; }
  100% {
    flex-direction: column;
    text-align: center; } }

@keyframes usage--flex-show {
  0% {
    flex-direction: column;
    text-align: center; }
  99% {
    flex-direction: column;
    text-align: center; }
  100% {
    flex-direction: row;
    text-align: left; } }

.usage-info--sidebar {
  min-width: 175px;
  padding: 15px 30px 12px 30px;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
  transition: padding 0.3s ease-in-out; }
  .usage-info--sidebar:after {
    content: '';
    width: 300px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    background-color: #242424; }
  .sidebar--loaded:not(.sidebar--collapsed) .usage-info--sidebar {
    animation: usage--sidebar-show 0.6s forwards alternate ease-in-out; }
  .sidebar--collapsed .usage-info--sidebar {
    padding: 14px 7px 12px 9px;
    animation: usage--sidebar-hide 0.6s forwards alternate ease-in-out; }

.usage-info__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  font-size: 13px; }
  .usage-info__list,
  .usage-info__list * {
    transition: 0s 0.3s; }
  .sidebar--loaded:not(.sidebar--collapsed) .usage-info__list {
    animation: usage--flex-show 0.3s forwards alternate ease-in-out; }
  .sidebar--collapsed .usage-info__list {
    animation: usage--flex-hide 0.3s forwards alternate ease-in-out; }

