.sidebar {
  width: 300px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  background-color: #282828;
  overflow: hidden;
  transition: width 0.3s ease-in-out; }
  .sidebar--collapsed {
    width: 96px; }

.sidebar__nav {
  margin: 0;
  padding: 0;
  list-style: none; }

.sidebar__nav__item {
  margin-right: 30px;
  margin-bottom: 39px;
  margin-left: 30px;
  position: relative; }
  .sidebar__nav__item:hover, .sidebar__nav__item.active {
    color: #6863ce; }
